import React from "react";

import api from "../../services/api";

import { useHistory } from "react-router-dom";
import { toast } from "react-toastify";

import {
  CancelPlanTittle,
  CancelPlanText,
  CancelPlanTextAsk,
  CancelPlanButton,
} from "./styles";

import { ModalContainer } from "../../shared/styledComponents";

function CancelPlan() {
  const history = useHistory();

  async function cancelSubscribe() {
    try {
      await api.post("/payments/cancelSubscribe");
      toast.success("Inscrição cancelada com sucesso");
      history.go(0)
    } catch (error) {
      toast.error("Erro inesperado");
    }
  }

  return (
    <ModalContainer>
      <CancelPlanTittle>Cancelar meu plano</CancelPlanTittle>
      <CancelPlanText>
        O cancelamento do plano não gera reembolso do valor pago até o presente
        momento.
      </CancelPlanText>
      <CancelPlanText>
        O cancelamento será efetivado no dia X do próximo mês.
      </CancelPlanText>
      <CancelPlanTextAsk>
        Tem certeza que deseja cancelar seu plano?
      </CancelPlanTextAsk>
      <CancelPlanButton onClick={cancelSubscribe}>CANCELAR</CancelPlanButton>
    </ModalContainer>
  );
}

export default CancelPlan;
