import React from "react";

import {
  HeaderContainer,
  LeftContainerHeader,
  SouFriburguense,
  RightContainerHeader,
  SmallLogoFriburguenseHeader,
  IconLogout,
  TitleHeader,
  ButtonLogin,
  LinkToRoot,
} from "./styles";

function Header({ name, logout, showModal }) {
  let headerContent = <ButtonLogin onClick={showModal}>Login</ButtonLogin>;

  if (name) {
    headerContent = (
      <>
        <TitleHeader>Olá {name}</TitleHeader>
        <IconLogout onClick={() => logout()} />
      </>
    );
  }

  return (
    <HeaderContainer>
      <LeftContainerHeader>
        <LinkToRoot href="/">
          <SmallLogoFriburguenseHeader />
        </LinkToRoot>

        <LinkToRoot href="/">
          <SouFriburguense>#SouFriburguense</SouFriburguense>
        </LinkToRoot>
      </LeftContainerHeader>
      <RightContainerHeader>{headerContent}</RightContainerHeader>
    </HeaderContainer>
  );
}

export default Header;
