import React, { useState, useContext } from "react";
import AuthContext from "../../contexts/auth";
import * as yup from "yup";
import {
  LoginContainer,
  BlueVerticalContainer,
  HeaderLoginContainer,
  HasAccount,
} from "./styles";

import {
  FillContainer,
  CommonTitle,
  CommonSubTitle,
  InputField,
  CommonInput,
  CommonLabelInput,
  RedButton,
  FormContainer,
} from "../../shared/styledComponents";
import { toast } from "react-toastify";

function Login() {
  const { signin } = useContext(AuthContext);

  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  async function handleSignin() {
    const loginSchema = yup.object().shape({
      email: yup.string().email().required(),
      password: yup.string().required().min(4).max(20),
    });

    const dataUser = {
      email: email,
      password: password,
    };

    const isValid = await loginSchema.isValid(dataUser);
    if (isValid) {
      await signin(email, password);
    } else {
      toast.error("Dados no formato errado");
    }
  }

  return (
    <LoginContainer>
      <BlueVerticalContainer></BlueVerticalContainer>
      <FillContainer>
        <HeaderLoginContainer>
          <CommonTitle>Login</CommonTitle>
          <CommonSubTitle>Faça login e entre na sua conta</CommonSubTitle>
        </HeaderLoginContainer>
        <FormContainer>
          <InputField>
            <CommonInput
              id="email"
              type="text"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
            />
            <CommonLabelInput link="email" value={email}>
              Seu email:
            </CommonLabelInput>
          </InputField>
          <InputField>
            <CommonInput
              id="password"
              type="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
            />
            <CommonLabelInput link="password" value={password}>
              Sua senha:
            </CommonLabelInput>
          </InputField>
          <RedButton as="button" onClick={handleSignin}>
            Entrar
          </RedButton>
        </FormContainer>
        <HasAccount as="a" href="/signup">
          Ainda não tem uma conta? Clique aqui e cadastre-se!
        </HasAccount>
      </FillContainer>
    </LoginContainer>
  );
}

export default Login;
