import { createContext, useState, useEffect } from "react";
import api from "../services/api";
import { signin, loadSignature } from "../services/user";
import { useHistory } from "react-router-dom";
import { ToastContainer, toast } from "react-toastify";

const AuthContext = createContext({
  signed: false,
  subscribed: false,
  loading: false,
  signature: {},
  token: "",
  user: {},
  handleSignin: function () {},
  handleLogout: function () {},
  handlePlan: function () {},
  plan: {},
});

export const AuthProvider = ({ children }) => {
  const [signed, setSigned] = useState(false);
  const [subscribed, setSubscribed] = useState(false);
  const [loading, setLoading] = useState(false);
  const [signature, setSignature] = useState({});
  const [user, setUser] = useState({});
  const [plan, setPlan] = useState({
    id: null,// 568447,
    name: null,//"Plano Ouro",
    amount: null//3999,
  });

  const history = useHistory();

  function handlePlan(plan) {
    setPlan(plan);
  }

  async function handleSignin(email, password) {
    try {
      const userToStore = await signin(email, password);
      if (userToStore) {
        setSigned(true);
        setUser(userToStore);
        history.push("/");
        toast.success("Logado com sucesso");
      } else {
        toast.error("Usuario ou senha incorretos");
      }

      const signaturePagarme = await loadSignature(userToStore);
      if (signaturePagarme) {
        setSignature(signaturePagarme);
        setSubscribed(true);
      }
    } catch (error) {}
  }

  function handleLogout() {
    localStorage.clear();
    setSigned(false);
    history.push("/");
  }

  useEffect(() => {
    async function loadStoragedData() {
      setLoading(true);
      const storagedUser = localStorage.getItem("user");
      const storagedToken = localStorage.getItem("token");
      if (storagedUser && storagedToken) {
        api.defaults.headers["Authorization"] = `Bearer ${storagedToken}`;
        const storagedUserParsed = JSON.parse(storagedUser);
        setUser(storagedUserParsed);
        setSigned(true);
        try {
          const signaturePagarme = await loadSignature(storagedUserParsed);
          if (signaturePagarme) {
            setSignature(signaturePagarme);
            setSubscribed(true);
          }
        } catch (error) {
          console.log(error);
        }
      }
      setLoading(false);
    }

    loadStoragedData();
  }, []);

  return (
    <AuthContext.Provider
      value={{
        signed: signed,
        token: subscribed,
        loading: loading,
        signature: signature,
        subscribed: subscribed,
        user: user,
        signin: handleSignin,
        logout: handleLogout,
        handlePlan: handlePlan,
        plan: plan,
      }}
    >
      <ToastContainer style={{ color: "white" }} />
      {children}
    </AuthContext.Provider>
  );
};

export default AuthContext;
