import React, { useContext } from "react";
import Header from "../../components/Header";
import Footer from "../../components/Footer";
import AuthContext from "../../contexts/auth";

import { slickPlans } from "../../configs/slick";
import Plan from "../../components/Plan";
import PlanMobile from "../../components/PlanMobile";
import Slider from "react-slick";

import { PlanTitle, PlanContainer, PlanContainerMobile } from "../Home/styles";
import { CenteredContainer } from "../../shared/styledComponents";

import { plans } from "../../services/plans";
import { useHistory } from "react-router";

function ChoosePlan() {
  const { user, logout, handlePlan } = useContext(AuthContext);
  const history = useHistory();

  const handleAssign = ({ idPagarme, title, amount }) => {
    const newPlan = {
      id: idPagarme,
      name: title,
      amount: amount,
    };
    handlePlan(newPlan);
    history.push("/payments");
  };


  return (
    <>
      <Header name={user.name} logout={logout} />
      <PlanContainer>
        <PlanTitle>Escolha seu plano:</PlanTitle>
        <CenteredContainer>
          {plans.map((elem) => (
            <Plan
              key={elem.id}
              idPlan = {elem.id}
              idPagarme={elem.id_pagarme}
              title={elem.name}
              amount={elem.amount}
              benefits={elem.benefits}
              bgImg={elem.bg_img}
              buttonTitle={"Assinar"}
              onAssign={handleAssign}
            />
          ))}
        </CenteredContainer>
      </PlanContainer>
      <PlanContainerMobile>
        <Slider {...slickPlans}>
          {plans.map((elem) => (
            <PlanMobile
              key={elem.id}
              idPagarme={elem.id_pagarme}
              title={elem.name}
              idPlan = {elem.id}
              amount={elem.amount}
              benefits={elem.benefits}
              bgImg={elem.bg_img}
              buttonTitle={"Assinar"}
              onAssign={handleAssign}
            />
          ))}
        </Slider>
      </PlanContainerMobile>
      <Footer />
    </>
  );
}

export default ChoosePlan;
