import React, { useState } from "react";
import { Tab, Tabs, TabList, TabPanel } from "react-tabs";
import "react-tabs/style/react-tabs.css";
import { customStyles } from "../../configs/modal";
import Modal from "react-modal";
import CancelPlan from "../CancelPlan";
import {
  UserInfo,
  UserInfoTitle,
  ActionsSubContainer,
  ActionButtonUpdate,
  ActionButtonCancel,
  ActionButtonSubscribe,
  TextButtonContent,
  ColumnContainerRow
} from "./styles.js";

import {
  WhiteContainer,
  CenterContainer,
  WhiteCard,
  CardsContainer,
  CommonSubTitle,
  CommonTitle,
  ColumnContainer,
} from "../../shared/styledComponents";




function ProfileContent({ user, signature, loading }) {
  const [showModal, setShowModal] = useState(false);

  const openModal = () => {
    setShowModal((prev) => !prev);
  };

  let SubscribePlanContent = (
    <TextButtonContent>
      <UserInfoTitle> Voce ainda não tem um plano </UserInfoTitle>
      <ActionButtonSubscribe as="a" href="/choosePlan">
        Assinar
      </ActionButtonSubscribe>
    </TextButtonContent>
  );

  const actions = (
    <ActionsSubContainer>
      <ActionButtonUpdate as="a" href="/updatePlan">
        Alterar plano
      </ActionButtonUpdate>
      <ActionButtonCancel onClick={openModal}>
        Cancelar plano
      </ActionButtonCancel>
    </ActionsSubContainer>
  );

  if (signature.data) {

    if (signature.data.status !== "canceled") {
      SubscribePlanContent = (
        <>
          <Modal
            ariaHideApp={false}
            isOpen={showModal}
            onRequestClose={() => setShowModal(false)}
            style={customStyles}
          >
            <CancelPlan />
          </Modal>
          <ColumnContainer>
            <ColumnContainerRow>
              <UserInfoTitle> Plano: </UserInfoTitle>
              <UserInfo>{signature.data && signature.data.planName}</UserInfo>
            </ColumnContainerRow>
            <ColumnContainerRow>
              <UserInfoTitle> Preço: </UserInfoTitle>
              <UserInfo>R$ {signature.data && signature.data.planPrice/100}</UserInfo>
            </ColumnContainerRow>
            <ColumnContainerRow>
              <UserInfoTitle> Status: </UserInfoTitle>
              <UserInfo>{signature.data && signature.data.status == "paid" ? "Pago" : signature.data.status }</UserInfo>
            </ColumnContainerRow>
          </ColumnContainer>
  
          {signature.data && signature.data.status !== "canceled" && actions}
        </>
      );
    }

    
  }

  let UserCardContent = <p>Voce ainda não tem uma carteirinha</p>;

  if (signature.data) {
    UserCardContent = (
      <>
        <UserInfoTitle> Código da Carteirinha: </UserInfoTitle>
        <UserInfo>{signature.data && signature.data.id}</UserInfo>
      </>
    );
  }

  
  return (
    <WhiteContainer>
      <Tabs>
        <TabList>
          <Tab>
            <CommonSubTitle>Meu Plano</CommonSubTitle>
          </Tab>
          <Tab>
            <CommonSubTitle>Meus dados</CommonSubTitle>
          </Tab>
        </TabList>
        <CenterContainer>
          <TabPanel>
            <CardsContainer>

              { loading  ?
              <>
                <WhiteCard>
                  <CommonTitle>Plano</CommonTitle>
                  {loading ? "Carregando..." : SubscribePlanContent}
                </WhiteCard>
                <WhiteCard>
                  <CommonTitle>Carteirinha</CommonTitle>
                  {loading ? "Carregando..." : UserCardContent}
                </WhiteCard>
              </>
            :
              <>
                <WhiteCard>
                  <CommonTitle>Plano</CommonTitle>
                  {loading ? "Carregando..." : SubscribePlanContent}
                </WhiteCard>
                <WhiteCard>
                  <CommonTitle>Carteirinha</CommonTitle>
                  {loading ? "Carregando..." : UserCardContent}
                </WhiteCard>
              </>
            }

              
            </CardsContainer>
          </TabPanel>
          <TabPanel>
            <CardsContainer>
              <WhiteCard>
                <CommonTitle>{user.name}</CommonTitle>
                <ColumnContainer>
                  <UserInfoTitle> Email: </UserInfoTitle>
                  <UserInfo>{user.email}</UserInfo>
                  <UserInfoTitle> Telefone: </UserInfoTitle>
                  <UserInfo>
                    ({user.ddd}) {user.number}
                  </UserInfo>
                </ColumnContainer>
              </WhiteCard>
              <WhiteCard>
                <CommonTitle>Endereço</CommonTitle>
                <ColumnContainer>
                  <UserInfoTitle> Rua:</UserInfoTitle>
                  <UserInfo>{user.street}</UserInfo>
                  <UserInfoTitle> Número:</UserInfoTitle>
                  <UserInfo>{user.street_number}</UserInfo>
                  <UserInfoTitle> Bairro:</UserInfoTitle>
                  <UserInfo>{user.neighborhood}</UserInfo>
                </ColumnContainer>
              </WhiteCard>
            </CardsContainer>
          </TabPanel>
        </CenterContainer>
      </Tabs>
    </WhiteContainer>
  );
}

export default ProfileContent;
