import React, { useState } from "react";
import Modal from "react-modal";
import Banner from "../../components/Banner";
import Login from "../../components/Login";
import Support from "../../components/Support";
import Footer from "../../components/Footer";
import Plan from "../../components/Plan";
import PlanMobile from "../../components/PlanMobile";
import { plans } from "../../services/plans";
import { slickPlans } from "../../configs/slick";
import { customStyles } from "../../configs/modal";

import Slider from "react-slick";

import { PlanTitle, PlanContainer, PlanContainerMobile } from "./styles";
import { CenteredContainer } from "../../shared/styledComponents";

const Home = () => {
  const [handleModal, setShowModal] = useState(false);

  const handleAssign = () => {
    setShowModal(!handleModal);
  };

  const showModal = () => {
    setShowModal(!handleModal);
  };

  return (
    <>
      <Modal
        ariaHideApp={false}
        isOpen={handleModal}
        onRequestClose={showModal}
        style={customStyles}
      >
      <Login />
      </Modal>
      <Banner showModal={showModal} />
      <PlanContainer>
        <PlanTitle>Escolha seu plano:</PlanTitle>
        <CenteredContainer padding={"2rem"}>
          {plans.map((elem) => {
            return <Plan
            key={elem.id}
            id={elem.id}
            title={elem.name}
            benefits={elem.benefits}
            bgImg={elem.bg_img}
            amount={elem.amount}
            onAssign={handleAssign}
            buttonTitle={"ASSINAR"}
          />
          }
          )
          }
        </CenteredContainer>
      </PlanContainer>
      <PlanContainerMobile>
        <Slider {...slickPlans}>
          {plans.map((elem) => (
            <PlanMobile
              key={elem.id}
              id={elem.id}
              title={elem.name}
              benefits={elem.benefits}
              bgImg={elem.bg_img}
              amount={elem.amount}
              onAssign={handleAssign}
              buttonTitle={"ASSINAR"}
            />
          ))}
        </Slider>
      </PlanContainerMobile>
      <Support />
      <Footer />
    </>
  );
};

export default Home;
