import React, { useContext } from "react";

import AuthRoutes from './auth.routes'
import SignedRoutes from './signed.routes'
import SubscribedRoutes from './subscribed.routes'

import AuthContext from '../contexts/auth.jsx'

const Routes = () => {

    const { signed, subscribed } = useContext(AuthContext)


    return signed && subscribed ? <SubscribedRoutes /> : signed ? <SignedRoutes /> : <AuthRoutes />

}

export default Routes