import React, { useRef, useCallback, useState, useReducer } from "react";
import Header from "../../components/Header";
import Login from "../../components/Login";
import Input from "../../components/Input";
import Footer from "../../components/Footer";

import { CellphoneContainer, Form } from "./styles";

import Modal from "react-modal";
import { customStyles } from "../../configs/modal";

import * as Yup from "yup";
import getValidationErrors from "../../utils/getValidationErrors";

import { CenteredContainer, RedButton } from "../../shared/styledComponents";

// Validação Campos
import InputMask from "react-input-mask";

import "../../utils/validateCPF";
import "../../utils/validateCelular";
import "../../utils/validateCEP";

import api from "../../services/api";

import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { useHistory } from "react-router";

const Cadastro = () => {
  const history = useHistory();
  const [handleModal, setShowModal] = useState(false);
  const formRef = useRef(null);

  const showModal = () => {
    setShowModal(!handleModal);
  };

  // initial State Form
  const initialFormState = {
    nome: "Guilherme",
    cpf: "12312312312",
    ddd: "21",
    celular: "987496026",
    logradouro: "Avenida das Américas",
    numero: "4666",
    complemento: "Barra Shopping",
    bairro: "Barra da Tijuca",
    cep: "22640902",
    email: "guilhermec.fialho@gmail.com",
    senha: "teste",
    confirmaSenha: "teste",
  };

  // Form
  const [form, setForm] = useState(initialFormState);

  // Style Input Mask
  const inputMaskDivStyle = {
    "width": "100%",
    "color": "#666360",
    "display": "flex",
    "justifyContent": "center",
    "alignItems": "center",
  }

  // Style Input Mask
  const inputMaskStyle = {
    "flex": "1",
    "marginLeft":"8px",
    "padding":"8px 10px",
    "background": "#F3E0E0",
    "border": 0,
    "outline": "none",
    "width": "400px",
    "fontSize": "14px",
  }

  const handleInputCEP = useCallback(async (event) => {
    let { value } = event.target;
    value = value.replace(".", "");

    const cepData = await api.get(`/users/find-cep/${value}`);

    if (cepData) {

      const { logradouro, bairro } = cepData.data

      setForm({...form, logradouro: logradouro, bairro: bairro});
    }
  }, []);

 
  const handleSubmit = async() => {
    try {

      const info = {
        name: form.nome,
        ddd: form.ddd,
        number: form.celular,
        document_number: form.cpf,
        email: form.email,
        password: form.senha,
        zipcode: form.cep,
        street_number: form.numero,
        complementary: form.complemento,
        neighborhood: form.bairro,
        street: form.logradouro,
      };
      
      const req = await api.post("/users/signup", info);


      console.log(req)
      toast.success("Cadastro efetuado com sucesso");

      history.push("/");

    } catch (err) {
      // const errors = getValidationErrors(err);
      // formRef.current?.setErrors(errors);
      toast.error("Erro no cadastro!");
    }
  };

  return (
    <>
      <Header showModal={showModal} />
      <Modal
        ariaHideApp={false}
        isOpen={handleModal}
        onRequestClose={showModal}
        style={customStyles}
      >
        <Login />
      </Modal>
      <CenteredContainer padding={"2rem 0rem"}>
        <Form ref={formRef} onSubmit={handleSubmit}>
          <Input 
            title={"Nome"} 
            onChange={(e) => setForm({...form, nome: e.target.value })}
            value={form.nome}
          />

          {/*                 CPF                       */}
          {/* <Input name={"CPF"} placeholder="Exemplo de CPF: 145123134134" /> */}
          <div style={inputMaskDivStyle}>
            <p>CPF</p>
            <InputMask
              style={inputMaskStyle}
              mask="99999999999"
              name="CEP"
              onKeyUp={handleInputCEP}
              onChange={(e) =>
                setForm({ ...form, cpf: e.target.value })
              }
              value={form.cpf}
            />
          </div>



          {/*                Visão Mobile                       */}
          {/* Vide cellphone container */}
          <Input
            containerStyle={{ width: "6rem", marginRight: "2rem" }}
            onChange={(e) => setForm({...form, ddd: e.target.value })}
            value={form.ddd}
            title={"DDD"}
          />


          <div style={inputMaskDivStyle}>
              <p>Celular</p>
              <InputMask
                style={inputMaskStyle}
                mask="999999999"
                onChange={(e) =>
                  setForm({ ...form, celular: e.target.value })
                }
                value={form.celular}
              />
            </div>


          {/*                 CEP                       */}
          <div style={inputMaskDivStyle}>
            <p>CEP</p>
            <InputMask
              style={inputMaskStyle}
              mask="99999999"
              name="CEP"
              onKeyUp={handleInputCEP}
              onChange={(e) =>
                setForm({ ...form, cep: e.target.value })
              }
              value={form.cep}
            />
          </div>
          <Input
            onChange={(e) => setForm({...form, logradouro: e.target.value})}
            value={form.logradouro}
            title={"Endereço"}
          />

          <Input
            onChange={(e) => setForm({...form, numero: e.target.value})}
            value={form.numero}
            title={"Número"}
          />
          

          
          <Input
            onChange={(e) => setForm({...form, complemento: e.target.value})}
            value={form.complemento}
            title="Complemento"
          />
          <Input
            onChange={(e) => setForm({...form, bairro: e.target.value})}
            value={form.bairro}
            title={"Bairro"}
          />
          <Input
            onChange={(e) => setForm({...form, email: e.target.value})}
            value={form.email}
            title={"Email"}
          />
          <Input 
            onChange={(e) => setForm({...form, senha: e.target.value})}
            type="password" title={"Senha"}  value={form.senha}
          />
          <Input 
            onChange={(e) => setForm({...form, confirmSenha: e.target.value})}
            value={form.confirmaSenha}
            type="password" title={"Confirmação de Senha"} 
          />
          <RedButton type="submit">Entrar</RedButton>
        </Form>
      </CenteredContainer>
      <Footer />
    </>
  );
};

export default Cadastro;
