import api from "./api";

export async function signin(email, password) {
  try {
    const resp = await api.post("/sessions/signin", {
      email: email,
      password: password,
    });

    const userToStore = {
      id: resp.data.id,
      name: resp.data.name,
      ddd: resp.data.ddd,
      number: resp.data.number,
      email: resp.data.email,
      street: resp.data.street,
      street_number: resp.data.street_number,
      neighborhood: resp.data.neighborhood,
    };

    api.defaults.headers["Authorization"] = `Bearer ${resp.data.token}`;


    localStorage.setItem("user", JSON.stringify(userToStore));
    localStorage.setItem("token", resp.data.token);

    return userToStore;
  } catch (error) {
    return false;
  }
}

export async function loadSignature(user) {
  try {
    const signaturePagarme = await api.get("/signature/get");

    if (signaturePagarme) {
      return signaturePagarme;
    }
  } catch (error) {
    return false;
  }
  return false;
}
