import React, { useContext, useState } from "react";
import Modal from 'react-modal'
import AuthContext from "../../contexts/auth";
import api from "../../services/api";
import { useHistory } from "react-router-dom";
import TermOfUse from '../TermOfUses/index';
import { PagarButton, ModalContainer, ModalHeader, ModalMain } from "./styles";
import {
  WhiteContainer,
  WhiteCard,
  CommonTitle,
  CommonSubTitle,
} from "../../shared/styledComponents";

import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

const customStyles = {
  content: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    width: '80%',
    height: '80%',
    top: '50%',
    left: '50%',
    right: 'auto',
    bottom: 'auto',
    marginRight: '-50%',
    transform: 'translate(-50%, -50%)',
  },
};


function PaymentsContent() {
  const { plan } = useContext(AuthContext);
  const history = useHistory();

  const [modalIsOpen, setIsOpen] = useState(false);
  let subtitle;

  function openModal() {
    setIsOpen(true);
    console.log(modalIsOpen);
  }

  function afterOpenModal() {
    // references are now sync'd and can be accessed.
    subtitle.style.color = '#000';
  }

  function closeModal() {
    setIsOpen(false);
  }

  function paySubscription() {
    const checkout = new window.PagarMeCheckout.Checkout({
      encryption_key: "ek_test_wWzLrAFwVq96Iv9rOL4lDFrtSTEJgb",
      success: async function (data) {
        try {
          let response = await api.post("/payments/createSubscribe", {
            card_hash: data.card_hash,
            plan_id: plan.id,
          });
          history.go(1);
          history.push("/");
          history.go(0);
        } catch (error) {
          toast.error(error);
        }
      },
      error: function (err) {
        console.log(err);
      },
      close: function () {
        console.log("The modal has been closed.");
      },
    });

    checkout.open({
      amount: plan.amount,
      customerData: "false",
      createToken: "false",
      paymentMethods: "credit_card",
    });
  }

  return (
    <WhiteContainer>
      <WhiteCard>
        <CommonTitle>Resumo</CommonTitle>
        <CommonSubTitle>Nome: {plan.name}</CommonSubTitle>
        <CommonSubTitle>Preço: {plan.amount/100}</CommonSubTitle>
        <CommonSubTitle>Recorrência: Mensal</CommonSubTitle>
        <div style={{ display: "flex" }}>
          <CommonSubTitle>
            Concordo com os <a style={{textDecoration: "underline", cursor: "pointer"}} onClick={openModal}>termos de uso</a> <input type="checkbox"></input>
          </CommonSubTitle>
        </div>

        <PagarButton onClick={paySubscription}>Pagar</PagarButton>
      </WhiteCard>
      <Modal
        isOpen={modalIsOpen}
        onAfterOpen={afterOpenModal}
        onRequestClose={closeModal}
        style={customStyles}
        contentLabel="Example Modal"
      >
        <ModalContainer>
          <ModalHeader>
            <div className="HeaderTitle">
              <h2 ref={(_subtitle) => (subtitle = _subtitle)}>TERMOS DE SERVIÇO</h2>
            </div>
            <div className="HeaderCloseButton">
              <button onClick={closeModal}>x</button>
            </div>
          </ModalHeader>
          <ModalMain>
            <TermOfUse/>
          </ModalMain>
        </ModalContainer>
      </Modal>
    </WhiteContainer>
  );
}

export default PaymentsContent;
