import * as Yup from 'yup';

function validateCep(strCep){
    const pattern = /^[0-9]{5}-[0-9]{3}$/;
    return pattern.test(strCep)
}

function cep(_, msg){
  return this.test({
    name: 'validateCep',
    message: msg,
    test: (value) => validateCep(value),
  });
}

Yup.addMethod(Yup.mixed, 'cep', cep);