import * as Yup from "yup";

function validateCelular(strCelular) {
  let regex = new RegExp("^(?:[2-8]|9[1-9])[0-9]{3}-[0-9]{4}$");
  return regex.test(strCelular);
}

function celular(_, msg) {
  return this.test({
    name: "validateCelular",
    message: msg,
    test: (value) => validateCelular(value),
  });
}

Yup.addMethod(Yup.mixed, "celular", celular);
